import * as React from 'react';
import { ToolSection } from '../framework/ToolSection';
import { syncer } from '../db/syncer';
import '../lib/dropboxApi';
import * as dropboxApi from '../lib/dropboxApi';

export const PlaygroundPage: React.FC<{}> = () => {
    const [dropboxFiles, setDropboxFiles] = React.useState<string[]|null>(null)

    async function listDropbox() {
        setDropboxFiles([...await dropboxApi.listFiles()]);
    }

    return (
        <div style={{height: '100%'}}>
            <ToolSection>
                <div className="button" onClick={listDropbox}>List</div>
                <div className="button" onClick={() => dropboxApi.uploadFile('test1', '{ "name": "test" }')}>Create Test</div>
                <div className="button" onClick={async () => console.log('getFile', await dropboxApi.getFile('test1'))}>Get Test</div>
                <div className="button" onClick={syncer.startDropboxServerSync}>Sync</div>
            </ToolSection>
            {dropboxFiles && dropboxFiles.map(f => <div key={f}>{f}</div>)}
        </div>
    );
}