import 'isomorphic-fetch';
import { ServerEntity } from './db/db';

export default class Api {
    static getSyncStatus(entites: string[]) {
        return fetch("/api/entity/getsyncstatus", {
            method: 'POST',
            headers: this.getHeaders(),
            body: JSON.stringify({ entites: entites || [] })
        }).then(response => {
            const contentType = response.headers.get("content-type");
            if(contentType && contentType.indexOf("application/json") !== -1) {
                return response.json() as Promise<{ fetch: string[], send: string[] }>;
            }
            throw new TypeError("Oops, we haven't got JSON!");
        });
    }

    static serverSyncFetch(ids: string[]) {
        return fetch("/api/entity/serversyncfetch", {
            method: 'POST',
            headers: this.getHeaders(),
            body: JSON.stringify({ ids: ids || [] })
        }).then(response => {
            const contentType = response.headers.get("content-type");
            if(contentType && contentType.indexOf("application/json") !== -1) {
                return response.json() as Promise<ServerEntity[]>;
            }
            throw new TypeError("Oops, we haven't got JSON!");
        });
    }

    static serverSyncSend(entities: ServerEntity[]) {
        return fetch("/api/entity/serversyncsend", {
            method: 'POST',
            headers: this.getHeaders(),
            body: JSON.stringify({ entities: entities || [] })
        }).then(response => {
        });
    }

    static testPush(subscription: any) {
        return fetch("/api/push/test", {
            method: 'POST',
            headers: this.getHeaders(),
            body: JSON.stringify(subscription)
        }).then(response => {
            console.log(response);
        });
    }

    private static getHeaders() {
        let headers = new Headers();
        headers.set('Content-Type', 'application/json');
        headers.set('Accept', 'application/json');
        return headers;
    }
}